






























































































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import api from "../../api";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  // 过滤器
  filters: {
    formatterAttrType
  }
})
export default class TypeManage extends Vue {
  pageDataIsLoading: boolean = true;
  doLoading: boolean = false;

  method: string = "add"; //保存方式，add：添加，edit：更新
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "name"
    }
  };
  // 选中表单信息
  basicForm: any = {};

  // 添加或编辑树节点弹窗
  dialogTitle: string = "";
  visibleDialog: boolean = false;
  // 弹窗表单信息
  formData: any = {
    id: "",
    name: "",
    code: "",
    icons: []
  };
  formRules: any = {
    name: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };
  transferData: any = {
    data: [],
    selected: []
  };
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("car");

  created() {
    this.getTypeTreeData().then(() => (this.pageDataIsLoading = false));
  }

  // 获取车辆类型树
  async getTypeTreeData() {
    try {
      let {
        data: { status, data, message }
      } = await this.$http.get(api.typeTree.getData);
      if (status) {
        this.treeMessage.data = data ? data : [];
        if (!this.basicForm.id && data) this.handleNodeClick(this.treeMessage.data[0]);
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      this.$message.error("请求错误，请重试！");
    }
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = Object.assign({}, data);
    if (this.basicForm.icon)
      this.basicForm.icons = [
        {
          name: "icon",
          url: this.basicForm.icon
        }
      ];
  }

  // 打开节点弹窗
  openTypeTreeNodeForm(buttonName: string = "add", data: any) {
    switch (buttonName) {
      case "add":
        this.method = "add";
        this.dialogTitle = "添加车辆类型";
        this.formData = {
          parentId: data.id,
          level: data.level + 1,
          icons: []
        };
        break;
      case "edit":
        this.method = "edit";
        this.dialogTitle = "编辑车辆类型";
        this.formData = Object.assign({}, data);
        if (this.formData.icon)
          this.formData.icons = [
            {
              name: "icon",
              url: this.formData.icon
            }
          ];
        break;
      case "addTop":
        this.method = "add";
        this.dialogTitle = "添加车辆类型";
        this.formData = { level: 1, icons: [] };
        break;
      default:
        this.formData = { icons: [] };
    }
    this.visibleDialog = true;
    this.getTypeAndAttrs();
  }

  // 获取组织机构和自定义属性，用于transfer
  async getTypeAndAttrs() {
    this.transferData.data = [];
    let {
      data: { data, status, message }
    } = await this.$http.get(api.transfer.getData + "?typeDetailId=" + this.formData.id);
    if (status) {
      this.transferData.data = data.data.map((i: any) => {
        return {
          key: i.id,
          label: i.attrName
        };
      });
      this.transferData.selected = data.selected;
    } else {
      this.$message.error(message);
    }
  }

  // 保存树节点
  async saveTypeAndAttr() {
    this.doLoading = true;
    (this.$refs.formData as any).validate((valid: any) => {
      if (!valid) {
        this.doLoading = false;
        return;
      }
    });

    let url = this.formData.id ? api.treeItem.update : api.treeItem.add;
    let typeVO = Object.assign(this.formData, { attrs: this.transferData.selected });
    if (typeVO.icons && typeVO.icons.length) {
      typeVO.icon = typeVO.icons[0].url ? typeVO.icons[0].url : "";
    } else {
      typeVO.icon = "";
    }
    try {
      let {
        data: { status, data, message }
      } = await this.$http.post(url, typeVO);
      if (status) {
        this.$message.success(message);
        this.visibleDialog = false;
        this.basicForm = Object.assign({}, data);
        (this.$refs["baseDataInfoTable"] as any).getCustomAttributeList();
        this.getTypeTreeData().then(() => (this.pageDataIsLoading = false));
      } else {
        this.$message.error(message);
      }
      this.doLoading = false;
    } catch (e) {
      this.$message.error("请求错误，请重试！");
    }
  }

  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.treeItem.delete + data.id).then((res: any) => {
        if (res.data.status) {
          this.$message.success("删除成功");
          this.basicForm = {};
          this.getTypeTreeData();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }
}
